* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  line-height: 150%;
}

a {
  color: #000;
  line-height: 150%;
  text-decoration: none;
}

input, textarea {
  background-color: #0000;
  border: none;
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 1000px #ffec7a;
}

input:-webkit-autofill:hover {
  -webkit-box-shadow: inset 0 0 0 1000px #ffec7a;
}

input:-webkit-autofill:focus {
  -webkit-box-shadow: inset 0 0 0 1000px #ffec7a;
}

.bebas-neue-regular {
